import React, {Component} from 'react';
import './mainDiv.css';

class Input extends Component{
    constructor(){
        super()
        this.state={
            text:""
        }
    }
    changeinput=(event)=>{
        this.setState({
            text:event.target.value
        })}
    render(){
    return <input type={this.props.type} placeholder={this.props.value} onChange={this.changeinput} className="inp" value={this.state.text}></input>
    }
}
export default Input;