import React, {Component} from 'react';
import BTN from './btn';
import Input from './Input'
import './mainDiv.css';
import {Route,Link} from 'react-router-dom';
class Div extends Component{
    render(){
    return <div className='division'> 
    <link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css"></link>
     <h1><u>Login</u></h1><div className="from">
     <i class="fa fa-user" aria-hidden="true"></i>
     <Input value="Username" type="text"></Input>
     <br></br>
     <i  class="fa fa-lock" aria-hidden="true"></i>
     <Input value="Password" type="password"></Input>
     <br></br></div>
   <BTN disptext="Log in"></BTN>
   <p className="p">Don't have an account? <Link to="/SignUp" className="sign">Signup</Link></p>
   <div className="icons">
   <a href="https://www.instagram.com/iamsrk/"> <i class="fa fa-instagram fa-2x bottom" aria-hidden="true" ></i></a>
   <i class="fa fa-facebook-official fa-2x bottom" aria-hidden="true"></i>
   <i class="fa fa-twitter-square fa-2x bottom" aria-hidden="true"></i>
   <i class="fa fa-linkedin-square fa-2x bottom" aria-hidden="true"></i>
   </div>
    </div>
    }
}
export default Div;